@import "../../Static/shared.css";

main {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 0;
}

/* Style the whole gallery image holder */
#gallery_images {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;

    padding: 10px;
    gap: 10px;

    width: 70%;
}

/* Style each image */
.gallery_image {
    width: 100%;
    cursor: pointer;
    position: relative;
}

#gallery_images .column {
    flex: 24%;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
}

.gallery_image img {
    object-fit: contain;
    width: 100%;
    height: 100%;
    vertical-align: middle;
    border-radius: 5px;
    border: 1px solid gray;
}

.gallery_image img:hover {
    border: 2px solid black;
}

/* Four-column fullscreen view */
@media screen and (max-width: 1650px) {

    #gallery_images {
        width: 95%;
    }
}

/* Two-column view */
@media screen and (max-width: 1250px) {

    #gallery_images {
        width: 95%;
    }

    #gallery_images .column {
        width: 50%;
        flex: 49%;
    }
}

/* One-column view */
@media screen and (max-width: 800px) {
    .gallery_image {
        height: auto;
        width: 100%;
    }

    .gallery_image:hover {
        height: auto;
    }

    #gallery_images {
        flex-direction: column;
        width: 95%;
    }

    #gallery_images .column {
        width: 100%;
    }
}